<template>
  <div class="">
      <!-- <div style="margin-bottom:50px ;">
        <el-page-header @back="goBack" content=""> </el-page-header>
      </div> -->
    <div class="activityList-box">
      
      <div class="activityList-view">
        <h1>{{ $t("activity.title") }}</h1>
        <ul>
          <li v-for="(item, index) in activityList" :key="index" @click="onDetails(item, index)">
            <div>
              <p>{{ item.title }}</p>
              <p>{{ item.describe }}</p>
              <p>{{ item.time }}</p>
            </div>
            <img :src="'./static/image/' + item.img" alt="" />
          </li>
        </ul>
      </div>
      <div class="follow">
        <h1>{{ $t("activity.followTitle") }}</h1>
        <div>
          <p v-for="(item, index) in followList" :key="index">
            <img :src="require('@/assets/activity/' + item.icon)" alt="" />
            <span>{{ item.title }}</span>
          </p>
        </div>
      </div>
    </div>
    <div style="  display: flex; align-items: center; justify-content:center;margin-bottom: 50px;">
      <el-pagination background layout="prev, pager, next" :total="total" @current-change="currentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data: () => {
    return {
      activityList: [],
      total: 0,
      query: {
        size: 10,
        page: 1
      }
    };
  },
  computed: {
    list() {
      return this.$t("activity.list");
    },
    followList() {
      return this.$t("activity.followList");
    },
  },
  created() {
    window.scrollTo({ top: 0 });
  },
  mounted() {
    this.intData()
    // this.activityList = this.list.reverse();
    // console.log(this.activityList);
  },
  methods: {
     goBack() {
      this.$router.back()
    },
    intData() {
      //引用axios
      const lang = localStorage.getItem("lang");
      switch (lang) {
        case 'en':
          axios.get("/static/lang/en.json", {})
            .then(({ data }) => {
              this.total = data.ActivityList.length
              console.log('获取到的json数据', data);
              const arr = data.ActivityList.reverse();
              this.activityList = this.paginateData(arr, this.query.page, 10)
            })
          break;
        case 'Czh':
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/Czh.json", {})
            .then(({ data }) => {
              this.total = data.ActivityList.length
              const arr = data.ActivityList.reverse();
              this.activityList = this.paginateData(arr, this.query.page, 10)

            })

          break;

        default:
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/zh.json", {})
            .then(({ data }) => {
              this.total = data.ActivityList.length
              const arr = data.ActivityList.reverse();
              this.activityList = this.paginateData(arr, this.query.page, 10)

            })

          break;
      }

    },
    onDetails(item, index) {
      localStorage.setItem("details", JSON.stringify(item))
      this.$router.push({ path: "/activity/details", });
    },
    paginateData(data, pageNumber, pageSize) {
      const startIndex = (pageNumber - 1) * pageSize;
      const endIndex = startIndex + pageSize;
      return data.slice(startIndex, endIndex);
    },
    currentChange(page) {
      this.query.page = page
      this.intData()
    }
  },
};
</script>


